<script>
import refreshLoading from '@/components/refreshLoading'
import {
  getRecommendList
} from '@/http/service/shop';
const commonEmpty = () => import('../component/commonEmpty');
const publicProductList = () => import(/* webpackChunkName: "productList" */'@/components/publicProductList/index')
/**
 * 生态优选
 * 推荐商品
 */
export default {
  components: {
    refreshLoading,
    publicProductList,
    commonEmpty,
  },
  data() {
    return {
      prodList: [],
      loading: false,
      finished: false,
      refreshing: false,
      contentType: '0',
      storeId: ''
    }
  },
  mounted() {
    this.contentType = this.$route.query.contentType;
    this.storeId = this.$route.query.storeId || '';
    this.$route.meta.title = this.contentType === '1' ? '生态优选' : '推荐商品';
    this.getList();
  },
  computed: {
    location() {
      return this.$store.getters['getlocationInfo']
    },
  },
  methods: {
    onLoad() {
      this.getList();
    },
    onRefresh() {
      this.getList();
    },
    getList() {
      const param = {
        storeModelId: this.$route.query.storeModelId || '',
        pageModelId: this.$route.query.pageModelId || '',
        productsIds: this.$route.query.productIds,
        streetId: this.location.townshipId,
        cityId: this.location.cityId,
        provinceId: this.location.provinceId,
        regionId: this.location.districtId,
        storeId: this.$route.query.storeId || ''
      };
      getRecommendList(param).then(res => {
        if (res.success && !!res.data) {
          this.prodList = res.data;
          this.prodList.map(item => {
            item.isLowestFlash = !!item.lowestFlashPrice || item.minPrice
          })
        } else {
          this.$toast(res.message || '获取数据失败，请稍后重试');
        }
        this.refreshing = false;
        this.loading = false;
        this.finished = true;
      });
    }
  },
}
</script>
